* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin: 20px 20px;
}

.country-list {
  list-style: none;
  margin-left: 20px;
  margin-right: 20px;
}

.list-item {
  display: flex;
  align-items: center;
  font-size: medium;
  padding-bottom: 5px;
}

.list-image {
  padding-right: 10px;
}

.country-card {
  margin-left: 20px;
}

.card-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.card-image {
  padding-right: 10px;
}

.card-title {
  font-size: 30px;
  font-weight: bold;
}
p {
  margin-bottom: 10px;
}

text {
  font-weight: bold;
  padding-right: 5px;
}
